import "react-responsive-carousel/lib/styles/carousel.min.css"; 
import { Carousel} from 'react-responsive-carousel';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";


const Sitcom = () => {

    const images = [
        { src : 'https://user-images.githubusercontent.com/43779189/147080304-c430a26b-c9f4-47f0-b5e4-8891bb22f739.png'},
    ]
    return (
        <>
            <Box sx={{backgroundColor:'#f8f9fa', mt:1, mb:1, p:2}}>
                <Typography variant="h5" textAlign="center" color="#636464">SITCOM</Typography>
                <Typography variant="subtitle2" textAlign="center" color="red"><i>status: ongoing project</i></Typography>
            </Box>
            
            <Paper elevation={6} sx={{ p:1 }}>
                <Carousel
                    autoPlay
                    interval={3000}
                    showArrows={true}
                    >
                    {images.map((image, index) => (
                        <div key={index}>
                            <img src={image.src}/>
                        </div>
                    ))}
                </Carousel>
            </Paper>

            <Box sx={{pt:1}}>
            <Accordion>
                    <AccordionSummary
                    expandIcon={<i className="fas fa-angle-down"></i>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    >
                    <Typography variant="subtitle1">About SITCOM</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="body1" color="initial">
                            Technologies being used:
                        </Typography>
                        <ul>
                            <li><Typography variant="body2" color="initial">Laravel</Typography></li>
                            <li><Typography variant="body2" color="initial">React JS</Typography></li>
                        </ul>
                        <Typography textAlign="justify">
                            Undergraduate capstone project
                        </Typography><br/>
                        <Typography variant="body1" color="initial">
                            {/* Live website : <a href="http://scheduler-app-tech.herokuapp.com/" target="_blank">http://scheduler-app-tech.herokuapp.com/</a> */}
                        </Typography>
                        <Typography variant="body1" color="initial">
                            {/* GitHub Repository : <a href="https://github.com/rocknrold/Scheduler" target="_blank">https://github.com/rocknrold/Scheduler</a> */}
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </Box>
        </>
    );
}
 
export default Sitcom;